          <td>　　其中：按税收规定比例扣除的职工教育经费</td>
          <td class="bg-write"><input type="text" name="5_1" value="0.00"/></td>
          <td class="bg-write"><input type="text" name="5_2" value="0.00"/></td>
          <td style="text-align: right">0.08</td>
          <td class="bg-unwrite"><input type="text" name="5_4" readonly value="0.00"/></td>
          <td class="bg-write"><input type="text" name="5_5" value="0.00"/></td>
