          <td class="bg-unwrite"><input type="text" name="5_7" readonly value="0.00"/></td>
        </tr>
        <tr>
          <td class="tdcon">6</td>
          <td>　　　　　按税收规定全额扣除的职工培训费用</td>
          <td class="bg-write"><input type="text" name="6_1" value="0.00"/></td>
          <td class="bg-write"><input type="text" name="6_2" value="0.00"/></td>
          <td class="bg-write"><input type="text" name="6_3" readonly value="1"/></td>
          <td class="tdcon">*</td>
          <td class="bg-unwrite"><input type="text" name="6_5" readonly value="0.00"/></td>
          <td class="bg-unwrite"><input type="text" name="6_6" readonly value="0.00"/></td>
          <td class="tdcon">*</td>
        </tr>
        <tr>
          <td class="tdcon">7</td>
          <td>四、工会经费支出</td>
          <td class="bg-write"><input type="text" name="7_1" value="0.00"/></td>
          <td class="bg-write"><input type="text" name="7_2" value="0.00"/></td>
          <td><input type="text" name="7_3" readonly value="0.02"/></td>
          <td class="tdcon">*</td>
          <td class="bg-write"><input type="text" name="7_5" value="0.00"/></td>
          <td class="bg-unwrite"><input type="text" name="7_6" readonly value="0.00"/></td>
          <td class="tdcon">*</td>
        </tr>
        <tr>
          <td class="tdcon">8</td>
          <td>五、各类基本社会保障性缴纳</td>
          <td class="bg-write"><input type="text" name="8_1" value="0.00"/></td>
          <td class="bg-write"><input type="text" name="8_2" value="0.00"/></td>
          <td class="tdcon">*</td>
          <td class="tdcon">*</td>
          <td class="bg-write"><input type="text" name="8_5" value="0.00"/></td>
          <td class="bg-unwrite"><input type="text" name="8_6" readonly value="0.00"/></td>
          <td class="tdcon">*</td>
        </tr>
        <tr>
          <td class="tdcon">9</td>
          <td>六、住房公积金</td>
          <td class="bg-write"><input type="text" name="9_1" value="0.00"/></td>
          <td class="bg-write"><input type="text" name="9_2" value="0.00"/></td>
          <td class="tdcon">*</td>
          <td class="tdcon">*</td>
          <td class="bg-write"><input type="text" name="9_5" value="0.00"/></td>
          <td class="bg-unwrite"><input type="text" name="9_6" readonly value="0.00"/></td>
          <td class="tdcon">*</td>
        </tr>
        <tr>
          <td class="tdcon">10</td>
          <td>七、补充养老保险</td>
          <td class="bg-write"><input type="text" name="10_1" value="0.00"/></td>
          <td class="bg-write"><input type="text" name="10_2" value="0.00"/></td>
          <td class="bg-write"><input type="text" name="10_3" readonly value="0.05"/></td>
          <td class="tdcon">*</td>
          <td class="bg-unwrite"><input type="text" name="10_5" readonly value="0.00"/></td>
          <td class="bg-unwrite"><input type="text" name="10_6" readonly value="0.00"/></td>
          <td class="tdcon">*</td>
        </tr>
        <tr>
          <td class="tdcon">11</td>
          <td>八、补充医疗保险</td>
          <td class="bg-write"><input type="text" name="11_1" value="0.00"/></td>
          <td class="bg-write"><input type="text" name="11_2" value="0.00"/></td>
          <td class="bg-write"><input type="text" name="11_3" readonly value="0.05"/></td>
          <td class="tdcon">*</td>
          <td class="bg-unwrite"><input type="text" name="11_5" readonly value="0.00"/></td>
          <td class="bg-unwrite"><input type="text" name="11_6" readonly value="0.00"/></td>
          <td class="tdcon">*</td>
        </tr>
        <tr>
          <td class="tdcon">12</td>
          <td>九、其他</td>
          <td class="bg-write"><input type="text" name="12_1" value="0.00"/></td>
          <td class="bg-write"><input type="text" name="12_2" value="0.00"/></td>
          <td class="tdcon">*</td>
          <td class="tdcon">*</td>
          <td class="bg-write"><input type="text" name="12_5" value="0.00"/></td>
          <td class="bg-unwrite"><input type="text" name="12_6" readonly value="0.00"/></td>
          <td class="bg-unwrite"><input type="text" name="12_7" readonly value="0.00"/></td>
        </tr>
        <tr>
          <td class="tdcon">13</td>
          <td>合计(1+3+4+7+8+9+10+11+12)</td>
          <td class="bg-unwrite"><input type="text" name="13_1" readonly value="0.00"/></td>
          <td class="bg-unwrite"><input type="text" name="13_2" readonly value="0.00"/></td>
          <td class="tdcon">*</td>
          <td class="bg-unwrite"><input type="text" name="13_4" readonly value="0.00"/></td>
          <td class="bg-unwrite"><input type="text" name="13_5" readonly value="0.00"/></td>
          <td class="bg-unwrite"><input type="text" name="13_6" readonly value="0.00"/></td>
          <td class="bg-unwrite"><input type="text" name="13_7" readonly value="0.00"/></td>
        </tr>
      </table>
    </form>
    <div style="margin-top: 15px" class="uk-text-center">
      <button @click="saveForm" class="uk-button uk-button-primary"><i class="uk-icon-save"></i> 保存</button>
      <button class="uk-button uk-button-primary" @click="dataRestore()"><i class="uk-icon-close"></i> 删除</button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-file-excel-o"></i> 导出Excel
      </button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-print"></i> 打印</button>
    </div>
  </div>
</template>

<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 16,  //当前表格的名称编号
        setting: null
      }
    },
    computed: {
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    mounted() {
      this.inputChangeEvent()
      this.inputClickEvent()
      this.getSetting()  //获取后台设置数据
    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        // axios.get('getSetting').then(res => {})
        //获取预设数据
        // this.setting = {'1_1': 10, '2_1': 5}
        this.initData()
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key] * 1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
        //第5列内的运算
        this.fn2(5, 12, [2], [1, 2, 3, 4, 5, 7, 8, 9, 12], 1)
        //第6列内的运算
        this.fn2(6, 12, [1, 5], [], 2)
        //第7列内的运算
        this.fn2(7, 12, [1, 4, 5], [1, 2, 3, 6, 7, 8, 9, 10, 11], 3)
        //第13行内的运算
        //第1列
        this.fn1('13_1', 1, [1, 3, 4, 7, 8, 9, 10, 11, 12])
        //第2列
        this.fn1('13_2', 2, [1, 3, 4, 7, 8, 9, 10, 11, 12])
        //第4列
        this.fn1('13_4', 4, [1, 3, 4, 7, 8, 9, 10, 11, 12])
        //第5列
        this.fn1('13_5', 5, [1, 3, 4, 7, 8, 9, 10, 11, 12])
        //第6列
        this.fn1('13_6', 6, [1, 3, 4, 7, 8, 9, 10, 11, 12])
        //第7列
        this.fn1('13_7', 7, [1, 3, 4, 7, 8, 9, 10, 11, 12])
        //第4行内的运算
        //  第1列
        this.fn1('4_1', 1, [5, 6])
        //  第2列
        this.fn1('4_2', 2, [5, 6])
        //  第4列
        this.fn1('4_4', 4, [5, 6])
        //  第5列
        this.fn1('4_5', 5, [5, 6])
        //  第6列
        this.fn1('4_6', 6, [5, 6])
        //  第7列
        this.fn1('4_7', 7, [5, 6])
      },
      // q 要计算单元格的name
      // h 参与运算的列
      // jump 参与运算的行
      fn1(q, h, jump) {
        var sum = 0
        for (let i = 0; i <= jump.length; i++) {
          let dom = J('input[name=' + jump[i] + '_' + h + ']')
          if (dom.val()) {
            sum += parseFloat(dom.val())
          }
        }
        J('input[name=' + q + ']').val(sum.toFixed(2))
      },
      // q 要运算的列
      // h 要运算多少行
      // r 参与运算的列
      // jump 需要跳过运算的行
      // type {
      //  1：a=b+c+d...
      //  2: a=b-c
      //  3: a=b+c-d
      // }
      fn2(q, h, r, jump, type) {
        for (let i = 1; i <= h; i++) {
          let sum = 0
          for (let k = 0; k < r.length; k++) {
            let dom = J('input[name=' + i + '_' + r[k] + ']')
            if (dom.val()) {
              if (type == 1) {
                sum += parseFloat(dom.val())
                if (jump) {
                  //判断是否是需要跳过运算的行
                  if (J.inArray(i, jump) < 0) {
                    J('input[name=' + i + '_' + q + ']').val(sum.toFixed(2))
                  }
                }
              } else if (type == 2) {
                if (k == 0) {
                  sum = parseFloat(dom.val())
                } else {
                  sum -= parseFloat(dom.val())
                }
                J('input[name=' + i + '_' + q + ']').val(sum.toFixed(2))
              } else if (type == 3) {
                if (k == 2) {
                  sum -= parseFloat(dom.val())
                } else {
                  sum += parseFloat(dom.val())
                }
                if (jump) {
                  //判断是否是需要跳过运算的行
                  if (J.inArray(i, jump) >= 0) {
                    J('input[name=' + i + '_' + q + ']').val(sum.toFixed(2))
                  }
                }
              }
            }
          }

        }
      }
    }
  }
</script>

<style scoped>
  table, table tr th, table tr td {
    font-family: '宋体';
    border: 1px solid #333;
  }

  table {
    line-height: 25px;
    text-align: center;
    border-collapse: collapse;
    padding: 2px;
  }

  input {
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
    padding: 0 2px;
    outline: 0px;
    text-align: right;
  }

  td {
    padding: 3px 2px;
    text-align: left;
  }

  .bg-write {
    background: #d6ebff;
  }

  .bg-unwrite {
    background: #ccccff;
  }

  .tdcon {
    text-align: center;
  }
</style>



